<template>
  <div>
    <the-header current-page-route="about"/>
    <section id="about" class="about section-show">

      <!-- ======= About Me ======= -->
      <div class="about-me container">

        <div class="section-title">
          <h2>About</h2>
          <p>Learn more about me</p>
        </div>

        <div class="row">
          <div class="col-lg-4" data-aos="fade-right">
            <img :src="myImage" class="img-fluid" alt="">
          </div>
          <div class="col-lg-8 pt-4 pt-lg-0 content" data-aos="fade-left">
            <h3>{{ aboutInfo.aboutMeTitle }}</h3>
            <p class="fst-italic">{{ aboutInfo.aboutMeShortDesc }}</p>
            <div class="row">
              <div class="col-lg-6">
                <ul>
                  <li><i class="bi bi-chevron-right"></i> <strong>Birthday:</strong>
                    <span>{{ aboutInfo.personalInfo.birthday }}</span></li>
                  <li><i class="bi bi-chevron-right"></i> <strong>Website:</strong> <a
                      :href="aboutInfo.personalInfo.websiteAddress"
                      target="_blank">{{ websiteName }}</a></li>
                  <li><i class="bi bi-chevron-right"></i> <strong>Phone:</strong>
                    <span>{{ aboutInfo.personalInfo.phoneNumber }}</span></li>
                  <li><i class="bi bi-chevron-right"></i> <strong>City:</strong>
                    <span>{{ aboutInfo.personalInfo.city }}</span>
                  </li>
                </ul>
              </div>
              <div class="col-lg-6">
                <ul>
                  <li><i class="bi bi-chevron-right"></i> <strong>Age:</strong>
                    <span>{{ aboutInfo.personalInfo.age }}</span>
                  </li>
                  <li><i class="bi bi-chevron-right"></i> <strong>Degree:</strong>
                    <span>{{ aboutInfo.personalInfo.degree }}</span></li>
                  <li><i class="bi bi-chevron-right"></i> <strong>Email:</strong>
                    <span>{{ aboutInfo.personalInfo.email }}</span>
                  </li>
                  <li><i class="bi bi-chevron-right"></i> <strong>Status:</strong>
                    <span>{{ aboutInfo.personalInfo.status }}</span></li>
                </ul>
              </div>
            </div>
            <p>{{ aboutInfo.aboutMeMoreDesc }}</p>
          </div>
        </div>

      </div>

      <!-- ======= Counts ======= -->
      <div class="counts container">
        <div class="row">
          <div class="col-lg-3 col-md-6">
            <div class="count-box">
              <i class="bi bi-emoji-smile"></i>
              <span>{{ happyClients }}</span>
              <p>Happy Clients</p>
            </div>
          </div>

          <div class="col-lg-3 col-md-6 mt-5 mt-md-0">
            <div class="count-box">
              <i class="bi bi-journal-richtext"></i>
              <span>{{ projects }}</span>
              <p>Projects</p>
            </div>
          </div>

          <div class="col-lg-3 col-md-6 mt-5 mt-lg-0">
            <div class="count-box">
              <i class="bi bi-headset"></i>
              <span>{{ hoursOfSupport }}</span>
              <p>Hours Of Support</p>
            </div>
          </div>

          <div class="col-lg-3 col-md-6 mt-5 mt-lg-0">
            <div class="count-box">
              <i class="bi bi-android2"></i>
              <span>{{ usedLanguage }}</span>
              <p>Used Languages</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import TheHeader from "@/components/UI/TheHeader";

export default {
  name: "AboutView",
  components: {
    TheHeader
  },
  data() {
    return {
      aboutInfo: {
        personalInfo: {}
      },
      timer: null,
      happyClients: 1,
      projects: 1,
      hoursOfSupport: 1,
      usedLanguage: 1
    };
  },
  computed: {
    websiteName() {
      const address = this.aboutInfo.personalInfo.websiteAddress;
      const executeReg = /^(http|https):\/\/(?<name>.+)?(?=\.).+/i.exec(address);
      if (executeReg !== null)
        return executeReg.groups.name;
      return null;
    },
    myImage() {
      if (this.aboutInfo.personalInfo.image !== undefined)
        return require(`@/assets/img/${this.aboutInfo.personalInfo.image}`);
      return null;
    }
  },
  methods: {
    increaseCount() {
      if (this.happyClients < this.aboutInfo.happyClients)
        this.happyClients++;
      if (this.projects < this.aboutInfo.projects)
        this.projects++;
      if (this.hoursOfSupport < this.aboutInfo.hoursOfSupport)
        this.hoursOfSupport++;
      if (this.usedLanguage < this.aboutInfo.usedLanguage)
        this.usedLanguage++;

      if (this.happyClients === this.aboutInfo.happyClients &&
          this.projects === this.aboutInfo.projects &&
          this.hoursOfSupport === this.aboutInfo.hoursOfSupport &&
          this.usedLanguage === this.aboutInfo.usedLanguage)
        clearInterval(this.timer);
    }
  },
  mounted() {
    this.aboutInfo = this.$store.getters.aboutPageInfo;
    this.timer = setInterval(this.increaseCount, 100);
  },
  unmounted() {
    clearInterval(this.timer);
  }
}
</script>

